import { REMOVE_COUPON } from "../types/types";

import errors from "./../messages/errors.json";
import axios from "axios";
// import { config } from "./products";

export const delete_orders = (choosedOrders) => async (dispatch) => {
	if (localStorage.getItem("access")) {
		const config = {
			headers: {
				"Content-Type": "application/json",
				Authorization: `JWT ${localStorage.getItem("access")}`,
				Accept: "application/json",
			},
		};
		const body = JSON.stringify({
			orders: [...choosedOrders],
		});
		try {
			const res = await axios.post(
				`${process.env.REACT_APP_API_VERSION_URL}/orders/delete/`,
				body,
				config
			);
			return res;
		} catch (error) {
			return errors.error3;
		}
	} else {
		return errors.error2;
	}
};

export const get_orders = (confi) => async (dispatch) => {
	if (localStorage.getItem("access")) {
		const config = {
			headers: {
				"Content-Type": "application/json",
				Authorization: `JWT ${localStorage.getItem("access")}`,
				Accept: "application/json",
			},
		};

		try {
			const res = await axios.get(
				`${process.env.REACT_APP_API_VERSION_URL}/orders/${
					confi ? `?${confi}` : ""
				}`,
				config
			);
			return res;
		} catch (error) {
			return errors.error3;
		}
	} else {
		return errors.error1;
	}
};

export const order = (formFields, cartItems, coupon) => async (dispatch) => {
	if (localStorage.getItem("access")) {
		const config = {
			headers: {
				"Content-Type": "application/json",
				Authorization: `JWT ${localStorage.getItem("access")}`,
				Accept: "application/json",
			},
		};
		const items = [];

		if (cartItems && Array.isArray(cartItems) && cartItems.length >= 1) {
			cartItems.map((item) => {
				items.push({
					size: item.size.id,
					product_id: item.id,
					product_quantity: item.quantity,
				});
				return items;
			});
		}

		const body = JSON.stringify({
			full_name: formFields.fullName,
			address: formFields.address,
			city: formFields.city,
			phone_number: formFields.phoneNumber,
			email: formFields.email,
			order_notes: formFields.notes,
			cartItems: [...items],
			code: coupon?.code,
		});
		try {
			const res = await axios.post(
				`${process.env.REACT_APP_API_VERSION_URL}/orders/`,
				body,
				config
			);
			dispatch({ type: REMOVE_COUPON });
			return res;
		} catch (error) {
			return errors.error3;
		}
	} else {
		return errors.error2;
	}
};

